import React,{ useState, useEffect } from 'react';
import { Container} from 'react-bootstrap';
import './Home.css';
import Work from './Work';
import Links from './Links';
import AOS from 'aos';
import 'aos/dist/aos.css';
import Skills from './Skills';
import About from './About';
import Contact from './Contact';
function Home({isHover, handleHover, handleClicked}) {
  const [scroll, setScroll] = useState(false);
  useEffect(() => {
    AOS.init();
    AOS.refresh();
  }, [])

  return (
    <Container style={{textAlign:'center'}}>
      <div className="page"  id="home" onWheel={() => setScroll(true)}>
        <div className="bref">
          <div className="vl" data-aos="fade" data-aos-delay="500" data-aos-duration="1500"></div>
          <div className="bref-intro">
            <h1

              data-aos="fade-right"
              data-aos-duration="1100"
              data-aos-delay="200"
              data-aos-once="true"
              data-aos-easing="ease-out-quad"
            >
              Hello, I'm Yuchen Pan
            </h1>
            <div data-aos="fade-left" data-aos-delay="400" data-aos-duration="1100" data-aos-easing="ease-out-quad" data-aos-once="true">
              <h3>A Full Stack engineer based in Chicago</h3>
              <div style={{textAlign: "left"}}>
                <Links />
              </div>
            </div>
          </div>

        </div>
        {scroll && <Skills />}
      </div>

      <div className="page" id="work">
        <div className="separator">
          My work
        </div>
        <Work handleClicked={handleClicked} />
      </div>
      <div className="page" id="about">
       <div className="separator">About Me</div>
        <About />
      </div>
      <div className="page" id="contact">
        <div className="separator">Contact</div>
        <Contact />
      </div>
    </Container>
  );
}

export default Home;