import './skills.css'
import docker from '../images/docker.svg';
import nginx from '../images/nginx.svg';
import aws from '../images/aws.svg';


const logo_img = {
  "docker": docker,
  "nginx": nginx,
  "aws": aws
};

const mySkills = {
  frontend: ["javascript","react","html5","jquery","css3","materialui","bootstrap", "redux"],
  backend: ["nodejs", "express", "mongodb", "postgresql", "mysql", "docker","nginx"],
  other:["webpack","babel","sass" ,"aws", "photoshop","premierepro", ]
}

function SkillImg ({skill, i, img}) {
  const srcImg = img ||`https://raw.githubusercontent.com/danielcranney/readme-generator/main/public/icons/skills/${skill}-colored.svg`
  return (
    <figure key={i} className="skill-img" style={{display:'inline-block', padding:'5px', textAlign:'center'}}>
      <figcaption style={{fontVariant:'small-caps'}}>{skill}</figcaption>
      <img
       className="scaled-img"
       src={srcImg}
       alt={skill}
      />
    </figure>
  )
}
function Skills () {
  return (
    <div id="skills" data-aos="fade-up">
      <h5 style={{fontWeight:300}}>
        Welcome to my personal website! Here you can learn more about me and my work.
      </h5>
     <div style={{display:'flex', justifyContent:'space-between'}}>
      {Object.keys(mySkills).map((key) => {
        return(
        <div key={key} style={{width:"30vw"}}>
          <h4 className="skill-cat">{key+" skills"}</h4>
          <div className="skill-container">
          {mySkills[key].map((skill, i) => {
            let img_src
            if (skill === 'docker' || skill === 'nginx' | skill === 'aws') {
              img_src = logo_img[skill]
            }
            return ( <SkillImg skill={skill} i={skill + i} img={img_src} />)
            })
          }
          </div>
        </div>
        )
      })
      }
     </div>
   </div>
  )
}

export default Skills;